.current_page:hover{
  background-color: #000;
  color: #ae3;
  text-decoration: none;
  
}

nav {
  margin-left: -8px;
  margin-right: -8px;
  text-align: center;
  mix-blend-mode: normal;
  //background: rgb(75,75,75);
  //background-image: url(/assets/images/castle_wall.gif);
  //background-repeat: repeat-x;
  margin: 0px 0px 0px;
  padding: 0px 0px 8px 0px;
  position: sticky;
  top: 0;
  z-index: 999998;
}

.navbox{
    background-color: rgba(10, 10, 10, 0.75);
    margin: 0px 5vw 1vw;
    border-radius: 5px;
    box-shadow: 0px 2px 4px #151617;
}

.nav-par {
  text-decoration: none;
}

.nav-button {
  display: inline-block;
  margin-top: 5px;
  text-decoration: none;
}

.nav-button{
  background-color: #222;
  color: #fff;
  //background-image: url(/assets/images/.png);
  padding: 5px;
  border: none; //0px solid crimson;
}

.current_page {
  background-color: #222;
  color: #fa9;
  text-decoration: none;
}

.nav-button:hover{
  background-color: #000;
  color: #ae3;
  text-decoration: none;
}

.content{
  margin: 0px 5vw 0px;
  text-align: center;
  border: 3px double transparent ;
  border-radius: 5px;
  background-color: rgba(10,10,10,0.75);  
  box-shadow: 0px 2px 4px #151617;

}

body{
  margin: 0px;
}


html {
  background-image: url(/assets/images/ss116.jpg); 
  font-family:"Times New Roman"; 
  color: #fff;
}
